.contacts__text {
  @media (min-width: @desktop-width) {
    width: 1300px;
    margin: 0 auto;
    position: relative;
  }

  &--inner-background {
    background-color: @basic-white;
  }
}

.contacts__wrapper {
  padding-top: 27px;
  padding-bottom: 25px;
  display: flex;

  @media (min-width: @tablet-width) {
    padding-top: 63px;
    padding-right: 72px;
    padding-bottom: 58px;
    padding-left: 71px;
    justify-content: space-between;
  }

  @media (min-width: @desktop-width) {
    padding-right: 89px;
    padding-left: 76px;
    position: absolute;
    width: 570px;
    z-index: 3;
    top: 96px;
    left: 40px;
    background: @basic-white;
  }

  &--inner-background {
    background-color: @basic-grey;
  }
}

.contacts__invitation {
  font-family: @oswald;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: @special-dark-grey-2;
  margin: 0;
  width: 50%;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 25px;
    padding-top: 3px;
    width: 300px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 2px;
  }

  &--form {
    @media (min-width: @tablet-width) {
      padding-top: 0;
      line-height: 27px;
    }
  }
}

.contacts__address {
  color: @special-dark-grey;
  margin: 0;
  width: 50%;
  padding-top: 2px;

  span {
    @media (min-width: @tablet-width) {
      display: block;
      margin-top: 19px;
    }
  }

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 21px;
    text-align: right;
    width: 300px;
    padding-top: 0;
  }

  @media (min-width: @desktop-width) {
    text-align: left;
    width: 186px;
    line-height: 20px;
  }
}
